import TShirtImages from "../tshirt-images/tshirt-images";
import invisibleMan from '../../images/pics/invisibleman.jpg'
import {useState} from "react";
import styles from './tshirt-image-picker.module.css';
import {Form} from "react-bootstrap";

const TShirtImagePicker = ({addImage, addImageFromFile}) => {
    const [fileData, setFileData] = useState(null);

    return (
        <div className={styles.tShirtImagePicker}>
            <div className={styles.images}><TShirtImages images={[
                invisibleMan,
                "data:image/jpeg;base64,",
                "data:image/png;base64,",
                "data:image/png;base64,%3D%3D",
                "data:image/png;base64,",
                "data:image/png;base64,",
            ]} addImage={addImage}/></div>
            <Form.Control type={"file"} onChange={e => {
    const reader = new FileReader();
    reader.onload = evt => {
        const imgObj = new Image();
        imgObj.src = evt.target.result;
        imgObj.onload = () => {
            setFileData(imgObj);
        }
    }
    reader.readAsDataURL(e.target.files[0]);
}}/>
            <button className={"btn btn-primary"} onClick={() => fileData ? addImageFromFile(fileData) : null}>Добавить фото</button>
        </div>
    );
}

export default TShirtImagePicker;